import React,{useState} from 'react'
import Success from './success';
import Failed from './failed';
const Contact = () => {

    const [success, setSuccess] = useState(false);
    const [failed, setFailed] = useState(false);
    const sendMsg=(e)=>{
        e.preventDefault();
        const name=document.getElementById("name");
        const email=document.getElementById("email");
        const msg=document.getElementById("msg");
        if(name.value!=="" && email.value!=="" && msg.value!=="")
        {
            setSuccess(true);
            setTimeout(()=>{
                setSuccess(false);
            },2000);
        }
        else{
            setFailed(true);
            setTimeout(()=>{
                setFailed(false);
            },2000);
        }
    }
    const clear=(e)=>{
        e.preventDefault();
        document.getElementById("name").value="";
        document.getElementById("email").value="";
        document.getElementById("msg").value="";
    }
    return (
        <div id="contact-sec">
            <div className="header">
                <i className="fas fa-paper-plane fa-2x"></i>
                <span className="primary">Contact us!</span>
            </div>
            <form id="contact-form" onSubmit={sendMsg}>
                <input name="name" id="name" type="text" placeholder="Enter your name"></input>
                <input name="email" id="email" type="email" placeholder="Enter your email-id"></input>
                <textarea name="message" id="msg" placeholder="Your message here"></textarea>
                <div className="buttons">
                    <input type="submit" value="Send Message"></input>
                    <button onClick={clear}>Clear</button>
                </div>
            </form>
            {success?<Success/>:null}
            {failed?<Failed/>:null}
        </div>
    )
}

export default Contact
