import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { useState, useEffect } from "react";
import Navbar from "./components/navbar";
import Scorecard from "./components/scorecard.js";
import Matches from "./components/matches";
import TitleCard from "./components/titlecard";
import Welcome from "./components/welcome";
import PlayerSearch from "./components/playersearch";
import About from "./components/about";
import Footer from "./components/footer";
import Loading from "./components/loading";
function App() {
  const [matches, setMatches] = useState([]);
  const [livmatches, setLivmatches] = useState([]);

  const fetchMatches = async () => {
    const res = await fetch(
      "https://api.cricapi.com/v1/matches?apikey=6d7d3568-e02b-4113-8c84-fa72407196fd&offset=0"
    );
    const resdt = await res.json();
    const matches = resdt.data;
    setMatches(matches);
  };
  const fetchLivMatches = async () => {
    const res = await fetch(
      "https://api.cricapi.com/v1/currentMatches?apikey=6d7d3568-e02b-4113-8c84-fa72407196fd&offset=0"
    );
    const resdata = await res.json();
    setLivmatches(resdata.data);
  };
  useEffect(() => {
    fetchLivMatches();
    fetchMatches();
  }, []);
  return (
    <Router>
      <div className="App">
        <div>
          <Navbar />
        </div>
        <div id="main-area">
          <header>
            <TitleCard />
            <Welcome />
          </header>
          <Switch>
            {livmatches.length > 0 ? (
              <Route
                exact
                path="/"
                render={(props) => <Scorecard livmatches={livmatches} />}
              ></Route>
            ) : null}
            {matches.length > 0 ? (
              <Route
                exact
                path="/Matches"
                render={(props) => <Matches matches={matches}></Matches>}
              ></Route>
            ) : (
              <Loading />
            )}
            <Route
              exact
              path="/PlayerSearch"
              render={(props) => <PlayerSearch />}
            ></Route>
            <Route exact path="/About" render={(props) => <About />}></Route>
          </Switch>
          <Footer />
        </div>
      </div>
    </Router>
  );
}

export default App;
