import React,{useState} from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

const Navbar = ({title,icon}) => {
    const [current, setCurrent] = useState(null);
    setTimeout(()=>{
        const url = window.location.href.split("/");
        let tab=url[url.length-1];
        if(tab==="")
        {
            tab="Home";
        }
        setCurrent(tab);
        document.getElementById(tab).style.background="#fff";
        document.getElementById(tab).style.color="#333";
    
    },1);
    const changeBg = (e)=>{
        if(e.target.id!==current && e.target.parentElement.id!==current)
        {
            let element;
            if(e.target.classList.contains("fas"))
            {
                element=e.target.parentElement;
            }
            else{
                element = e.target;
            }
            element.style.background="#fff";
            element.style.color="#333";
            document.getElementById(current).style.background="none";
            document.getElementById(current).style.color="#fff";
            setCurrent(element.id);
        }
        else{
            return;
        }

    }
    return (
        <nav id="main-navbar">
            <div id="nav-tools">
                <Link to='/' className="Link" id="Home" onClick={changeBg}>
                    <i className="fas fa-home fa-2x"></i>
                </Link>
                <Link to='/Matches' className="Link" id="Matches" onClick={changeBg}>
                    <i className="fas fa-calendar-alt fa-2x"></i>
                </Link>
                <Link to='/PlayerSearch' className="Link" id="PlayerSearch" onClick={changeBg}>
                    <i className="fas fa-chart-pie fa-2x"></i>
                </Link>
                <Link to='/About' className="Link" id="About" onClick={changeBg}>
                    <i className="fas fa-info-circle fa-2x"></i>
                </Link>
            </div>
        </nav>
    )
}

Navbar.prototype={
    title:PropTypes.string.isRequired,
    icon:PropTypes.string.isRequired
}
Navbar.defaultProps={
    title:"CricLiv",
    icon:"fas fa-star fa-2x"
}

export default Navbar
