import React, { useEffect, useState } from "react";

const Player = ({ stat }) => {
  const [bstat, setbstat] = useState({});
  const getStats = (stat) => {
    const bstats = {};
    stat.map((data) => {
      if (
        data.fn == "batting" &&
        data.matchtype == "odi" &&
        data.stat == "runs"
      )
        bstats["odi_r"] = data.value;
      if (data.fn == "batting" && data.matchtype == "odi" && data.stat == "hs")
        bstats["odi_hs"] = data.value;
      if (data.fn == "batting" && data.matchtype == "odi" && data.stat == "avg")
        bstats["odi_avg"] = data.value;
      if (
        data.fn == "batting" &&
        data.matchtype == "t20" &&
        data.stat == "runs"
      )
        bstats["t20_r"] = data.value;
      if (data.fn == "batting" && data.matchtype == "t20" && data.stat == "hs")
        bstats["t20_hs"] = data.value;
      if (data.fn == "batting" && data.matchtype == "t20" && data.stat == "avg")
        bstats["t20_avg"] = data.value;
    });
    setbstat(bstats);
  };
  useEffect(() => {
    getStats(stat.stats);
  }, []);
  return (
    <div id="player">
      <i className="fas fa-star fa-2x"></i>
      <span className="title"> {stat.name}</span>
      <div id="bio">
        {stat.playerImg ? (
          <img
            src={stat.playerImg}
            alt="playerimage"
            className="profile-pic"
          ></img>
        ) : (
          <img
            src="https://www.pngall.com/wp-content/uploads/5/User-Profile-PNG.png"
            alt="playerimage"
            className="profile-pic"
          ></img>
        )}
        {stat.profile !== "" ? (
          <h1 className="secondary">{stat.profile}</h1>
        ) : (
          <h1 className="secondary">
            Profile Deatils haven't been uploaded yet.
          </h1>
        )}
      </div>
      <div className="grid">
        <div id="info">
          <h1>Personal Deatils :</h1>
          <div className="info-grid">
            <p className="secondary">Name</p>
            <p className="secondary">{stat.name}</p>
          </div>
          {/* <div className="info-grid">
                        <p className="secondary">Full Name</p>
                        <p className="secondary">{stat.name}</p>
                    </div> */}
          <div className="info-grid">
            <p className="secondary">DOB</p>
            <p className="secondary">
              {stat.dateOfBirth.slice(0, stat.dateOfBirth.length - 9)}
            </p>
          </div>
          {/* <div className="info-grid">
                        <p className="secondary">Born</p>
                        <p className="secondary">{stat.born}</p>
                    </div> */}
        </div>
        <div id="cric-info">
          <h1>Player Deatils :</h1>
          <div className="info-grid">
            <p className="secondary">Batting Style</p>
            <p className="secondary">{stat.battingStyle}</p>
          </div>
          <div className="info-grid">
            <p className="secondary">Bowling Style</p>
            <p className="secondary">{stat.bowlingStyle}</p>
          </div>
          <div className="info-grid">
            <p className="secondary">Role</p>
            <p className="secondary">{stat.role}</p>
          </div>
          <div className="info-grid">
            <p className="secondary">Country</p>
            <p className="secondary">{stat.country}</p>
          </div>
        </div>
        <div id="batting-stats">
          <h1>Batting Stats : </h1>
          <div className="stats">
            <div className="formats">
              <p className="secondary">Format</p>
              <p className="secondary">Runs</p>
              <p className="secondary">Highest</p>
              <p className="secondary">Average</p>
            </div>
            <div className="formats">
              <p className="secondary">ODIs</p>
              <p className="secondary">
                {bstat["odi_r"] ? bstat["odi_r"] : "-"}
              </p>
              <p className="secondary">
                {bstat["odi_hs"] ? bstat["odi_hs"] : "-"}
              </p>
              <p className="secondary">
                {bstat["odi_avg"] ? bstat["odi_avg"] : "-"}
              </p>
            </div>
            <div className="formats">
              <p className="secondary">T20s</p>
              <p className="secondary">
                {bstat["t20_r"] ? bstat["t20_r"] : "-"}
              </p>
              <p className="secondary">
                {bstat["t20_hs"] ? bstat["t20_hs"] : "-"}
              </p>
              <p className="secondary">
                {bstat["t20_avg"] ? bstat["t20_avg"] : "-"}
              </p>
            </div>
            {/* <div className="formats">
              <p className="secondary">Tests</p>
              <p className="secondary">
                {stat.data.batting.tests ? stat.data.batting.tests.Runs : "-"}
              </p>
              <p className="secondary">
                {stat.data.batting.tests ? stat.data.batting.tests.HS : "-"}
              </p>
              <p className="secondary">
                {stat.data.batting.tests ? stat.data.batting.tests.Ave : "-"}
              </p>
            </div> */}
          </div>
        </div>
        {/* <div id="bowling-stats">
          <h1>Bowling Stats : </h1>
          <div className="stats">
            <div className="formats">
              <p className="secondary">Format</p>
              <p className="secondary">Wickets</p>
              <p className="secondary">Best</p>
              <p className="secondary">Economy</p>
            </div>
            <div className="formats">
              <p className="secondary">ODIs</p>
              <p className="secondary">
                {stat.data.bowling.ODIs ? stat.data.bowling.ODIs.Wkts : "-"}
              </p>
              <p className="secondary">
                {stat.data.bowling.ODIs ? stat.data.bowling.ODIs.BBI : "-"}
              </p>
              <p className="secondary">
                {stat.data.bowling.ODIs ? stat.data.bowling.ODIs.Econ : "-"}
              </p>
            </div>
            <div className="formats">
              <p className="secondary">T20s</p>
              <p className="secondary">
                {stat.data.bowling.T20Is ? stat.data.bowling.T20Is.Wkts : "-"}
              </p>
              <p className="secondary">
                {stat.data.bowling.T20Is ? stat.data.bowling.T20Is.BBI : "-"}
              </p>
              <p className="secondary">
                {stat.data.bowling.T20Is ? stat.data.bowling.T20Is.Econ : "-"}
              </p>
            </div>
            <div className="formats">
              <p className="secondary">Tests</p>
              <p className="secondary">
                {stat.data.bowling.tests ? stat.data.bowling.tests.Wkts : "-"}
              </p>
              <p className="secondary">
                {stat.data.bowling.tests ? stat.data.bowling.tests.BBI : "-"}
              </p>
              <p className="secondary">
                {stat.data.bowling.tests ? stat.data.bowling.tests.Econ : "-"}
              </p>
            </div>
          </div>
        </div> */}
      </div>
    </div>
  );
};

export default Player;
