import React, { useState, useEffect } from "react";
import Score from "./score";
import Loading from "./loading";
const Scorecard = ({ livmatches }) => {
  const [scorecard, setScorecard] = useState([]);
  const fetchScore = async () => {
    const data = livmatches.map((match) => {
      const score = {};
      score.title = match.name;
      score.team1 = match.teams[0];
      score.team2 = match.teams[1];
      score.score = match.score;
      return score;
    });

    // const res = await fetch(
    //   `https://api.cricapi.com/v1/cricScore?apikey=6d7d3568-e02b-4113-8c84-fa72407196fd&unique_id=${id}`
    // );
    // const resdata = await res.json();
    const array = scorecard;
    array.push(data);
    console.log(array);
    setScorecard(...array);
  };
  useEffect(() => {
    fetchScore();
    // eslint-disable-next-line
  }, []);
  return (
    <div id="liv-matches">
      <i className="fas fa-satellite-dish fa-2x"></i>
      <span className="primary">Live Scores</span>
      {scorecard.length === livmatches.length ? (
        <div id="score-cards">
          {livmatches.map((x, index) => (
            <Score scorecard={scorecard[index]} />
          ))}
        </div>
      ) : null}
      {scorecard.length === 0 ? <Loading /> : null}
    </div>
  );
};

export default Scorecard;
