import React from 'react'
import Contact from './contact';
import Founder from './founder';
const About = () => {
    return (
        <div id="about">
            <div className="header">
                <i className="fas fa-lightbulb fa-2x"></i>
                <span className="primary">About this App :</span>
            </div>
            <div className="info">
                <h2>CricLive Version 1.0.0</h2>
                <h1 className="secondary">CricLive App gives you updates about Live scores, schedules of upcoming Cricket matches and also you can also search for the stats of any player using the Player stats section.</h1>
            </div>
            <Contact/>
            <Founder/>
        </div>
    )
}

export default About
