import React, { useEffect, useState, useLayoutEffect } from "react";
const Players = ({ players, selectplayer, clear }) => {
  const getPID = (e) => {
    const id = e.target.id;
    const pid = players[`${id}`].id;
    console.log(pid);
    selectplayer(pid);
    clear(null);
  };
  const [playerList, setplayerList] = useState([]);
  const getImg = async () => {
    const array = await players.map(async (player) => {
      const res = await fetch(
        `https://api.cricapi.com/v1/players_info?apikey=6d7d3568-e02b-4113-8c84-fa72407196fd&id=${player.id}`
      );
      const resdata = await res.json();
      const notAvaliable = "https://h.cricapi.com/img/icon512.png";
      const alternatePlayer =
        "https://www.pngall.com/wp-content/uploads/5/User-Profile-PNG.png";
      player.playerImg =
        resdata.data.playerImg == notAvaliable
          ? alternatePlayer
          : resdata.data.playerImg;
      console.log(player.playerImg);
      return player;
    });
    console.log(array);
    setplayerList(array);
  };
  useLayoutEffect(() => {
    console.log(players.object);
  }, []);
  return (
    <div id="players">
      <h1 className="secondary">Player names matched as per your search :</h1>
      {players.map((player, index) => (
        <div id={index} className="player" onClick={getPID}>
          <img
            id={index}
            className="player-image"
            src={player.playerImg}
            alt="player-img"
            onError={(e) => {
              e.target.src =
                "https://www.pngall.com/wp-content/uploads/5/User-Profile-PNG.png";
            }}
          ></img>
          {/* {player.playerImg ? (
            <img
              id={index}
              className="player-image"
              src={player.playerImg}
              alt="player-img"
            ></img>
          ) : (
            <img
              id={index}
              className="player-image"
              src="https://www.pngall.com/wp-content/uploads/5/User-Profile-PNG.png"
            ></img>
          )} */}

          <h1 id={index} className="secondary">
            {player.name}
          </h1>
        </div>
      ))}
    </div>
  );
};

export default Players;
