import React from "react";

const Score = ({ scorecard }) => {
  // const splitRuns=()=>{
  //     const scores=scorecard.description;
  //     const tscore=scores.split("v");
  //     const teams={
  //         team1:tscore[0].match(/\d+/g),
  //         team2:tscore[1].match(/\d+/g)
  //     }
  //     return teams;
  // }
  return (
    <div id="score-card">
      <div id="score-title">
        <div>
          <i className="fas fa-trophy fa-2x"></i>
        </div>
        <h1 className="secondary">{scorecard["title"]}</h1>
        {/* <h1 className="secondary">Vs</h1>
                <h1 className="secondary">{scorecard["team2"]}</h1> */}
      </div>
      <div className="runs">
        <div className="team1">
          <h2>{scorecard["team1"]}</h2>
          <h2>
            {/* {splitRuns().team1!==null?splitRuns().team1.map((x,index,array)=>{
                        if(index%2===0)
                        return <span>{x}/</span>
                        else if(index!==array.length-1)
                        return <span>{x} &&nbsp;</span>
                        else
                        return <span>{x}</span>
                        }):<span>0/0</span>} */}

            {scorecard.score.map((score) => {
              if (score.inning.includes(scorecard["team1"])) {
                return (
                  <span>
                    {score.r}/{score.w}
                  </span>
                );
              }
            })}
          </h2>
        </div>
        <div className="team2">
          <h2>{scorecard["team2"]}</h2>
          <h2>
            {/* {splitRuns().team2!==null?splitRuns().team2.map((x,index,array)=>{
                        if(index%2===0)
                        return <span>{x}/</span>
                        else if(index!==array.length-1)
                        return <span>{x} &&nbsp;</span>
                        else
                        return <span>{x}</span>
                        }):<span>0/0</span>} */}
            {scorecard.score.map((score) => {
              if (score.inning.includes(scorecard["team2"])) {
                return (
                  <span>
                    {score.r}/{score.w}
                  </span>
                );
              }
            })}
          </h2>
        </div>
      </div>
    </div>
  );
};

export default Score;
