import React from "react";
import { useState } from "react";
import Player from "./player";
import Players from "./players";
import Alert from "./alert";
import Loading from "./loading";
const PlayerSearch = () => {
  const [players, setPlayers] = useState(null);
  const [stat, setStat] = useState(null);
  const [userInput, setUserInput] = useState("");
  const [loading, setLoading] = useState(false);
  const onSubmit = (e) => {
    e.preventDefault();
    const playerName = document.getElementById("playername").value;
    if (playerName !== "" && playerName.match(/^[0-9]+$/) === null) {
      setLoading(true);
      setStat(null);
      getPlayers(playerName);
      document.getElementById("playername").value = "";
      setUserInput(playerName);
    } else {
      setUserInput(null);
      setTimeout(() => {
        setUserInput("");
      }, 2000);
    }
  };
  const getPlayers = async (playerName) => {
    const res = await fetch(
      `https://api.cricapi.com/v1/players?apikey=6d7d3568-e02b-4113-8c84-fa72407196fd&offset=0&search=${playerName}`
    );
    const resdata = await res.json();
    const players = resdata.data;
    const array = players.map((player) => {
      // const res = await fetch(
      //   `https://api.cricapi.com/v1/players_info?apikey=6d7d3568-e02b-4113-8c84-fa72407196fd&id=${player.id}`
      // );
      // const resdata = await res.json();
      // const notAvaliable = "https://h.cricapi.com/img/icon512.png";
      // const alternatePlayer =
      //   "https://www.pngall.com/wp-content/uploads/5/User-Profile-PNG.png";
      player.playerImg = `https://h.cricapi.com/img/players/${player.id}.jpg`;
      //   (await resdata.data.playerImg) == notAvaliable
      //     ? alternatePlayer
      //     : await resdata.data.playerImg;
      // console.log(player.playerImg);
      return player;
    });
    console.log(array);
    setLoading(false);
    setPlayers(array);
  };
  const getPlayerStat = async (pid) => {
    setLoading(true);
    const res = await fetch(
      `https://api.cricapi.com/v1/players_info?apikey=6d7d3568-e02b-4113-8c84-fa72407196fd&id=${pid}`
    );
    const resdata = await res.json();
    setLoading(false);
    setStat(resdata.data);
  };
  return (
    <div id="player-search">
      <i className="fas fa-search fa-2x"> </i>
      <span className="primary"> Players Stats :</span>
      <p className="secondary">
        Search the stats of any cricket player using his name.
      </p>
      <form id="search-form" onSubmit={onSubmit}>
        <input
          type="text"
          id="playername"
          placeholder="Enter player name..."
        ></input>
        <input type="submit" value="Get Stats" />
      </form>
      {loading === true && userInput !== null ? <Loading /> : null}
      {userInput === null ? <Alert /> : null}
      {players !== null && loading !== true ? (
        <Players
          players={players}
          selectplayer={getPlayerStat}
          clear={setPlayers}
        />
      ) : null}
      {players === null &&
      stat === null &&
      loading === false &&
      userInput !== null ? (
        <div id="search-results">
          <i className="fas fa-user-secret fa-4x"></i>
          <h1 className="secondary">Search results will appear here..</h1>
        </div>
      ) : null}
      {stat !== null && loading !== true ? <Player stat={stat} /> : null}
    </div>
  );
};

export default PlayerSearch;
