import React from 'react'

const Match = ({match}) => {
    const searchMatch=()=>{
        window.location.href=`https://www.google.com/search?q=${match.name}${match.date}`;
    }
    return (
        <div className="match" onClick={searchMatch}>
            <i className="fas fa-trophy"></i>
            <span className="secondary"> {match.name}</span>
            <br></br>
            <div className="date">{match.date}</div>
        </div>
    )
}

export default Match
